.footer-grid {
    display: grid;
    grid-template-rows: repeat(3, auto);
    height: auto;
    /*align-content: center;*/
    /* Dark Blue Gradient */
    background: linear-gradient(225deg, #16192C 0%, rgba(33, 33, 66, 0.95) 100%);
    border-radius: 12px 12px 0px 0px;
    align-items: center;
}

.footer-logo-social {
    display: grid;
    grid-template-columns: 57% auto;
    margin-left: 12vw;
    margin-top: 2vw;
}

.footer-logo-container {
    display: flex;
    align-items: center;
    justify-content: start;
}

.footer-social {
    display: grid;
    grid-template-columns: 4.2vw 4.2vw 4.2vw;
    justify-content: center;
    align-items: center;
    margin-top: 0.5vw;
}


.footer-nav {
    display: grid;
    grid-template-columns: 57% auto;
    margin-left: 10vw;
    margin-top: 1.5vw;
}

.footer-left {
    display: flex;
    justify-content: start;
}

.footer-right {
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer-credits {
    display: grid;
    grid-template-columns: 80% auto;
    margin-left: 10vw;
    margin-top: 1.5vw;
    margin-bottom: 1.7vw;
}

.footer-logo {
    width: 9vw;
    padding-top: 11px;
}

.footer-img {
    width: 11vw;
    margin-left: 5.2vw;
}

.footer-a {
    color: white;
    padding-left: 2vw;
    font-size: 1vw;
    font-size: clamp(11px, 1vw, 18px);
}

.footer-text {
    font-size: 1vw;
    font-size: clamp(11px, 1vw, 18px);
    color: white;
    padding-left: 2vw;
}

.float-right {
    float: right;
    margin-left: 22vw;
}

.footer-circle {
    background: #f3f9ff;
    border: 0.1px solid rgba(196, 196, 196, 0.5);
    box-sizing: border-box;
    box-shadow: 0px 30px 40px rgba(212, 217, 232, 0.2);
    border-radius: 57px;
    width: 2.3vw;
    height: 2.3vw;
    margin-left: 3vw;
    display: flex;
    justify-content: center;
}

.footer-icon {
    width: 60%;
}

.footer-icon-facebook {
    width: 40%;
}

.footer-facebook {
    margin-left: 45vw;
}

.footer-grid-movil {
    display: none;
}

@media screen and (max-width: 952px) {
    .footer-grid-movil {
        display: grid;
        grid-template-rows: repeat(10, auto);
        height: fit-content;
        /*align-content: center;*/
        /* Dark Blue Gradient */
        background: linear-gradient(225deg, #16192C 0%, rgba(33, 33, 66, 0.95) 100%);
        border-radius: 12px 12px 0px 0px;
        justify-content: center;
        align-items: center;
    }

    .footer-img-movil1 {
        width: 45vw;
        margin-top: 17vw;
        border-radius: 5px;
    }

    .footer-img-movil2 {
        width: 45vw;
        margin-top: 10vw;
        border-radius: 5px;
    }

    .footer-grid {
        display: none;
    }

    .footer-movil-menu {
        margin-top: 8vw;
        display: grid;
        grid-template-rows: repeat(5, 11vw);
        margin-bottom: 8vw;
    }

    .footer-a {
        font-size: 4vw;
        text-align: center;
    }

    .footer-social {
        margin-bottom: 30px;
        grid-template-columns: repeat(3, 12vw);
    }

    .footer-circle {
        width: 2rem;
        height: 2rem;
    }
}