.company-grid {
    background-image: url("https://firebasestorage.googleapis.com/v0/b/punto-banana.appspot.com/o/Ingenali%2FEmpresa-bg.svg?alt=media&token=c60b95b8-51f0-44de-b8d8-5319d0cfdb8f");
    background-position: center;
    /* Center the image */
    background-repeat: no-repeat;
    /* Do not repeat the image */
    background-size: cover;
    /* Resize the background image to cover the entire container */
    /*border-radius: 18px;*/
    display: grid;
    grid-template-rows: auto auto;
    height: auto;
    margin-top: 7vw;
}

.company-small-grid {
    display: grid;
    grid-template-columns: 30% 70%;
    cursor: pointer;
}

.company-small-grid:hover {
    transform: scale(1.05);
}

.company-up {
    display: flex;
    align-content: center;
    justify-content: center;
    margin-top: 2vw;
}

.company-down {
    margin-top: 13vw;
    display: grid;
    grid-template-columns: auto auto;
    margin-bottom: 19vw;
}

.company-left {
    display: grid;
    grid-template-rows: repeat(5, clamp(8vw, 6.25vw, 120px));
    padding-left: 18vw;
}

.company-right {
    margin-top: 1vw;
}

.company-small-left {
    display: flex;
    align-items: center;
    justify-content: center;
}

.company-small-right {
    display: flex;
    align-items: left;
    justify-content: center;
}

.company-title {
    font-size: 4vw;
    font-weight: bold;
    line-height: 6vw;
    color: white;
    text-align: center;
    margin-bottom: -6vw;
}

.company-card-sub {
    color: #812F49;
    font-size: 1.3vw;
    text-transform: uppercase;
    text-align: center;
    padding-top: 0.5vw;
}

.company-text {
    font-size: 18px;
    line-height: 32px;
    max-width: 45ch;
}

.company-img {
    width: 34vw;
}

.company-card {
    background-color: white;
    width: 31vw;
    box-shadow: 3px 8px 8px 0 rgba(0, 0, 0, 0.6);
    transition: 0.3s;
    border-radius: 5px;
    height: 6vw;
}

.company-card:hover {
    transform: scale(1.05);
}

.small-icon {
    height: 4vw;
}

.margin-adjust {
    margin-top: -12.5vw;
}

@media screen and (max-width: 952px) {
    
    .company-grid {
        height: fit-content;
        justify-content: center;
        background-image: none;
        background-color: #26368B;
        border-radius: 10px;
        grid-template-rows: auto;
        width: 90vw;
        margin-left: 3.3vw;
    }
    
    .company-img {
        display: none;
    }

    .company-down {
        grid-template-columns: auto;
        justify-content: center;
        margin-top: 10vw
      }

    .company-left {
        display: grid;
        grid-template-rows: repeat(5, 65vw);
        justify-items: center;
        padding-left: 0vw;
        margin-bottom: -12vw;
    }

    .company-right {
        display: none;
    }

    .company-small-grid {
        /*grid-template-columns: 35% auto;*/
        grid-template-columns: 100%;
        align-items: center;
        justify-content: center;
        height: 100%;
        grid-template-rows: auto auto;
    }

    .company-title {
        width: 60vw;
        line-height: 8.6vw;
        font-size: 5.8vw;
        text-align: center;
        margin-top: 8vw;
        margin-bottom:3vw;
    }
    .company-card {
        height: 54vw;
        width: 50vw;
        border-radius: 3rem;
        padding-left: 0vw;
    }

    .company-small-right {
        display: grid;
        grid-template-rows: auto;
        text-align: center;
    }

    .small-icon {
        height: 20vw;
        padding-top: 5vw;
    }

    .company-card-sub {
        font-size: 5.3vw;
        text-transform: none;
        margin-top: 0vw;
    }
    
    .company-text {
        font-size: 25px;
    }

    .margin-adjust {
        margin-top: 0px;
    }
}

@media screen and (max-width: 700px) {
    .small-icon {
        /*height: 4em;*/
    }
}