.clients-grid {
    display: grid;
    grid-template-rows: auto auto;
    justify-content: center;
    margin-top: 6vw;
} 

.clients-title {
    font-size: 4.4vw;
    font-weight: bold;
    line-height: 92px;
    color: black;
    text-align: center;
}

.clients-text {
    font-size: 18px;
    line-height: 32px;
    text-align: center;
}

.clients-img {
    width: 80vw;
}

.clients-img-mov {
    display: none;       
}

@media screen and (max-width: 952px) {

    .clients-alignment {
        display: flex;
        justify-content: center;
        margin-bottom: 20vw;
    }

    .clients-grid {
        margin-top: 10vw;
        display: grid;
        grid-template-rows: auto auto;
        grid-template-columns: auto;
        height: auto;
        justify-content: center;
        justify-items: center;
        background: #ECF1F8;
        /* Dark Shadow */
        box-shadow: 0px 10px 20px rgba(41, 41, 42, 0.07);
        border-radius: 12px;
        width: 90vw;
    }
    
    .clients-title {
        font-size: 7vw;
        font-weight: bold;
        line-height: 4rem;
        color: black;
        text-align: center;
    }
    
    
    .clients-img {
        display: none;
    }

    .clients-img-mov {
        display: block;
        width: 70vw;
        margin-top: 2vw;
    }
}