.form-card {
    /*background-size: cover;
    background-repeat: no-repeat;*/
    background-image: url("https://firebasestorage.googleapis.com/v0/b/punto-banana.appspot.com/o/Ingenali%2FContacto-bg.svg?alt=media&token=fc247832-c390-4952-89d8-daa1003dd52a");
    background-position: center;
    height: 55vh;
    width: 70vw;
    transition: 0.3s;
    border-radius: 23px;
    display: grid;
    grid-template-columns: 60% auto;
    margin-top: 0vw;
    margin-bottom: 8vw;
}

.left-grid {
    display: grid;
    align-items: center;
    justify-content: center;
    grid-template-rows: auto auto;
}

.grid-center {
    display: grid;
    align-items: center;
    justify-content: center;
}

.form-left {
    padding-left: 1vw;
}

.form-right {
    display: grid;
    justify-content: center;
    width: 80%;
    grid-template-columns: auto;
    grid-template-rows: repeat(auto-fill);
}

.form-card-sub {
    color: white;
    font-size: 2.7vw;
    line-height: 60px;
    width: 60%;
    padding-left: 20%;
}

.contacto-img {
    padding-left: 35%;
    width: 30%;
    margin-top: -9vw;

}

.form-text {
    color: white;
    font-size: 1.5vw;
    padding-top: 1.3vw;
    text-align: center;
    margin-bottom: 0.3vw;
}

.form-input {
    width: auto;
    height: 3vw;
    border-radius: 6px;
    border-style: none;
    font-size: 1.2vw;
    box-shadow: 3px 3px 3px 0 rgba(0, 0, 0, 0.6);
    padding-left: 1vw;
}

.form-button {
    width: auto;
    height: 50px;
    background: linear-gradient(296.51deg, #9b2327 0%, #BE4448 100%);
    color: white;
    border-radius: 4px;
    border-style: none;
    font-size: 18px;
    box-shadow: 3px 3px 2px 0 rgba(0, 0, 0, 0.6);
    margin-bottom: 1vw;
}

@media screen and (max-width: 952px) {

    .contacto-img {
        display: none;
    }

    .form-card {
        height: fit-content;
        width: 90vw;
        transition: 0.3s;
        border-radius: 23px;
        display: grid;
        grid-template-columns: auto;
        margin-bottom: 17vw;
        grid-template-rows: auto auto;
    }

    .form-card-sub {
        color: white;
        font-size: 7vw;
        line-height: 9vw;
        width: 90%;
        padding-left: 5%;
    }

    .form-left {
        text-align: center;
    }
    
    .form-right {
        display: grid;
        justify-content: center;
        justify-items: center;
        align-items: center;
        width: 100%;
        grid-template-columns: auto;
        grid-template-rows: repeat(5, 15vw);
    }

    .form-text {
        color: white;
        font-size: 4.5vw;
        margin-top: -11vw;
        text-align: center;
        width: 70%;
    }
    
    .form-input {
        width: 70%;
        height: 10vw;
        border-radius: 4px;
        border-style: none;
        font-size: 4vw;
        padding-left:2vw;
        box-shadow: 3px 3px 3px 0 rgba(0, 0, 0, 0.6);
        margin-top: -7vw;
    }
    
    .form-button {
        width: 72%;
        height: 10vw;
        background: linear-gradient(296.51deg, #9b2327 0%, #BE4448 100%);
        color: white;
        border-radius: 4px;
        border-style: none;
        font-size: 1.2rem;
        box-shadow: 3px 3px 2px 0 rgba(0, 0, 0, 0.6);
        margin-top: -1vw;
        margin-bottom: 8vw;
    }

}