

.team-left {
    padding-left: 12vw;
    margin-top: 7vw;
}

.team-img {
    width: 33vw;
    padding-left: 0vw;
    margin-top: 4vw;
}

.team-right {
    padding-right: 10vw;
}

.team-title {
    font-size: 3vw;
    font-weight: bold;
    line-height: 55px;
    color: black;
    text-align: center;
    margin-top: 10vw;
}

.team-text {
    font-size: 1.1vw;
    line-height: 2.6vw;
    width: 35%;
    margin-top: -20vw;
    margin-bottom: 8vw;
    padding-left: 54vw;
}

@media screen and (max-width: 952px) {

    .team-grid {
        justify-content: center;
        background: #ECF1F8;
        /* Dark Shadow */
        box-shadow: 0px 10px 20px rgba(41, 41, 42, 0.07);
        border-radius: 12px;
        width: 90vw;
        height: auto;
        margin-top: -33vw
    }

    .team-alignment {
        display: grid;
        grid-template-rows: auto;
        justify-content: center;
        justify-items: center;
        margin-bottom: 5rem;
    }

    .team-title {
        font-size: 6vw;
        line-height: 2.5rem;
        background-color: #26368B;
        border-radius: 8px;
        color: white;
        padding: 10px;
        box-shadow: 1.5px 3px 2px 0 rgba(0, 0, 0, 0.6);
        width: 300px;
        height: fit-content;
        z-index: 1;
        margin-top: -2vw;
    }

    .team-grid-center {
        grid-template-rows: auto;
    }

    .team-text {
        width: 80%;
        font-size: 3vw;
        line-height: 6vw;
        padding-left: 14%;
        margin-top: 8vw;
        text-align: center;
    }

    .team-img {
        margin-left: 2vw ;
        width: 60vw;
        margin-top: 32vw;
    }

}