.experience-card {
    display: grid;
    /*grid-template-columns: repeat(4, 25%);*/
    grid-template-columns: repeat(3, 33%);
    justify-content: center;
    background-color: white;
    width: 70vw;
    height: 10vw;
    padding: 1.7vw 0px;
    box-shadow: 0px 0px 1px rgba(12, 26, 75, 0.1), 0px 20px 24px rgba(20, 37, 63, 0.06);
    border-radius: 15px;
}

.exp-col {
    display: grid;
    grid-template-rows: auto;
    justify-items: center;
}

.exp-mini-card {
    display: grid;
    grid-template-rows: 50% 50%;
    justify-items: center;
}

.exp-number-big {
    font-size: 4vw;
    font-weight: 990;
    margin-top: 1.2vw;
}

.exp-text {
    font-size: 1.4vw;
    font-weight: 300;
    margin-top: 0.5vw;
}

.exp-dark-red {
    color: #812F49;
}

.exp-light-red {
    color: #F16063;
}


@media screen and (max-width: 952px) {

    .experience-card {
        background-color: #26368B;
        grid-template-rows: repeat(3, auto);
        grid-template-columns: 100%;
        height: fit-content;
        width: 90vw;
        margin-top: 8vw;
    }

    .exp-col {
        text-align: center;
        margin-top: 5vw;
    }

    .exp-number-big {
        color: white;
        font-size: 8vw;
    }

    .exp-text {
        color: white;
        font-size: 3vw;
    }
}