.home-separator {
    display: grid;
    grid-template-rows: 80% auto;
    align-items: center;
    justify-items: center;
    margin-top: 5vw;
}

.home-grid {
    display: grid;
    grid-template-columns: 50vw auto;
    align-items: center;
    justify-items: center;
}

.home-left {
    padding-left: 10vw;
    padding-top: 2vw;
}

.home-right {
    padding-right: 10vw;
}

.home-title {
    font-size: 4vw;
    font-weight: bold;
    font-family: Inter;
}

.home-text {
    font-size: 1.2vw;
    width: 85%;
    line-height: 32px;
}

.home-img {
    width: 32vw;
    margin-top: 2vw;
}

.home-img2 {
    display: block;
    width: 32vw;
    padding-top: 4vw;
}

.home-img3 {
    display: none;
}

.home-button-blue {
    /*width: 115px;*/
    width: 13vw;
    height: 3.8vw;
    background: var(--newBlue);
    border: .5px solid rgba(0, 0, 0, .25);
    box-sizing: border-box;
    box-shadow: 0 30px 40px rgba(212, 217, 232, .2);
    border-radius: 6px;
    margin-top: 1.5vw;
    cursor: pointer;
}

.home-button-blue:hover {
    transform: scale(1.05);
}

