.home-button-text {
    font-family: Inter;
    font-style: normal;
    font-weight: 700;
    font-size: 1.2vw;
    text-align: center;
    color: #fff;
}

.blue-text {
    color: #2E46C5;
}

@media screen and (max-width: 952px) {
    .home-separator {
        display: grid;
        grid-template-rows: 80% auto;
        height: auto;
        align-items: center;
        justify-items: center;
        margin-top: 19vw;
        margin-bottom: 9vh;
    }
    .home-grid {
        grid-template-columns: auto;
        grid-template-rows: auto auto;
        justify-items: center;
        justify-content: center;
        margin-bottom: 10vw;
        width: 90%;
    }
    .home-right {
        grid-row: 1;
    }
    .home-left {
        display: grid;
        padding-left: 0%;
        padding-right: 0%;
        justify-content: center;
        justify-items: center;
        text-align: center;
        width: 90%;
    }
    .home-title {
        font-size: 8vw;
        margin-top: -2.5vw;
    }
    .home-text {
        font-size: 4vw;
        line-height: 7vw;
        width: 70vw;
        margin-top: 0vw;
    }
    .home-button-blue {
        margin-top: 2.5vw;
        width: 37vw;
        height: 10vw;
    }

    .home-button-text {
        font-size: 3.5vw;
    }

    .home-img {
        width: 90%;
        margin-left: 5%;
    }
    
    .home-img2 {
        display: none;
    }

    .home-img3 {
        display: block;
        width:40vw;
        margin-top: -4vw;
    }
}