.service-grid {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: center;
    margin-top: 8vw;
}


.service-title {
    font-size: 2.6vw;
    color: black;
    text-align: center;
}

.flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.grid-center {
    display: grid;
    grid-template-rows: auto auto;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.service-text {
    font-size: 1.4vw;
    line-height: 32px;
    text-align: center;
    width: 90%;
}

.service-img {
    width: 35vw;
    padding-left: 12vw;
    padding-right: 1vw;
}

.service-img2 {
    display: none;
}

.service-left {
    display: flex;
    align-items: center;
    justify-content: center;
}

.service-right {
    display: flex;
    align-items: center;
    justify-content: center;
}

.service-center {
    display: grid;
    
    justify-items: center;
    padding-right: 10vw;
}

.service-h2-title-flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.service-h2-title {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: #26368B;
    color: white;
    border-radius: 15px;
    width: 60vw;
    height: 11vw;
    font-size: 2.5vw;
    margin-top: 8vw;
    margin-bottom: 3vw;
}

@media screen and (max-width: 952px) {

    .service-h2-title-flex {
       display: none;
    }

    .service-grid {
        display: grid;
        grid-template-columns: auto;
        grid-template-rows: auto auto ;
        height: auto;
        justify-content: center;
        background: #ECF1F8;
        /* Dark Shadow */
        box-shadow: 0px 10px 20px rgba(41, 41, 42, 0.07);
        border-radius: 12px;
        width: 90vw;
        margin-top: -10vw;
    }
    .service-left {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .service-img {
        display: none;
    }

    .service-img2 {
        display: block;
        width: 60vw;
        margin-top: 4vw;
    }

    .service-alignment {
        display: flex;
        justify-content: center;
        margin-bottom: 5rem;
    }

    .service-center {
        display: grid;
        grid-template-rows: auto auto;
        justify-items: center;
        padding-right: 0vw;
    }

    .service-title {
        font-size: 5.5vw;
        line-height: 2.5rem;
        background-color: #26368B;
        border-radius: 8px;
        color: white;
        padding: 10px;
        box-shadow: 1.5px 3px 2px 0 rgba(0, 0, 0, 0.6);
        width: 80%;
        height: fit-content;
        margin-top: 9vw;
    }

    .service-text {
        width: 85%;
        font-size: 3.3vw;
        line-height: 2.0rem;
        text-align: center;
        margin-bottom: 7vw;
        margin-top: -4vw;
    }
}